  font-weight: bold;
  font-size: 16px;
  text-align: center;
  font-family: var(--app-text-main-font);

  p {
    text-align: center;
    font-size: 26px;
    color: var(--app-color-gray-dark);

  }
}

.itemsTable {
  z-index: 0;
  width: inherit;

  table {

    border-spacing: 0;
    border-top: 0px solid #bbb;
    border-left: 0px solid #bbb;
    border-right: 0px solid #bbb;

    th:last-child,
    td:last-child {
      color: var(--app-color-gray-dark);
    }

    tr {
      :first-child {
        font-size: 16px;
        font-weight: bold;

      }

      :last-child {
        border-right: 0;
      }
    }

    th {
      :first-child {
        font-size: 16px;
        text-align: left;
      }

      text-align: left;
      color: white;
      background-color: var(--app-color-gray-dark);
      //border-right: 1px solid #bbb;
    }

    td {
      text-align: center;
      font-size: 16px;
      margin: 0;
      border-bottom: 0px solid #bbb;
      border-right: 0px solid #bbb;

    }
  }
}

.avatarContentContainer {
  margin: 0 10px 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.avatarUrlImg {
  object-fit: cover;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: black;
  border: 1px solid gray;
}