.tray {
  width: 100%;
  background: var(--white);
  color: var(--darkest-blue);
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.tray-buttons-container {
  padding: 10px;
  display: flex;
}

.tray-buttons-container button {
  padding: 0;
  background-color: white;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-weight: normal;
}

.tray-buttons-container button svg {
  color: #121a24;
}

.tray-buttons-container button + button {
  margin-left: 2rem;
}

.controls,
.actions,
.leave {
  width: 100%;
  display: flex;
  align-items: center;
}

.controls {
  justify-content: center;

}
.controls button {
  height: 80px;
  width: 30%;
  border: 1px solid gray;
  padding:10px;
  margin: 10px;
}
.actions {
  justify-content: center;

}
.actions button {
  height: 60px;
  width: 80px;
  border: 1px solid gray;
}
.leave {
  justify-content: flex-end;
  height: 60px;
}
.leave button {
  height: 60px;
  width: 80px;
  border: 1px solid gray;
}
